.header {
  background-color: $springster-purple;
  position: relative;
  &__search {
    .header__search-input, .header__search-input + * {
      display: none;
    }
    .header__search-input:checked + * {
      display: block;
    }
    .header__search-bar {
      position: relative;
    }
  }
}
